.confirmOrderPage {
  height: 100vh;
  background-color: white;
  display: grid;
  grid-template-columns: 6fr 3fr;
}

.confirmOrderPage > div:last-child {
  border-left: 1px solid rgba(0, 0, 0, 0.247);
}

.confirmshippingArea {
  padding: 5vmax;
  padding-bottom: 0%;
}

.confirmshippingArea > p {
  font: 400 1.8vmax "Roboto";
}

.confirmshippingAreaBox,
.confirmCartItemsContainer {
  margin: 2vmax;
}

.confirmshippingAreaBox > div {
  display: flex;
  margin: 1vmax 0;
}

.confirmshippingAreaBox > div > p {
  font: 400 1.4vmax "Roboto";
  color: black;
}
.confirmshippingAreaBox > div > span {
  margin: 0 1vmax;
  font: 100 1.3vmax "Roboto";
  color: #575757;
}

.confirmCartItems > p {
  font: 400 1.8vmax "Roboto";
}

.confirmCartItems {
  padding: 5vmax;
  padding-top: 2vmax;
}

.confirmCartItemsContainer {
  max-height: 20vmax;
  overflow-y: auto;
}

.confirmCartItemsContainer > div {
  display: flex;
  font: 400 1vmax "Roboto";
  align-items: center;
  justify-content: space-between;
  margin: 2vmax 0;
}

.confirmCartItemsContainer > div > img {
  width: 8vmax;
  height: 8vmax;
}

.confirmCartItemsContainer > div > a {
  color: #575757;
  margin: 0 2vmax;
  width: 60%;
  text-decoration: none;
}

.confirmCartItemsContainer > div > span {
  font: 100 0.9vmax "Roboto";
  color: #5e5e5e;
  width: 350px;
}

.orderSummary {
  padding: 7vmax;
}

.orderSummary > p {
  text-align: center;
  font: 400 1.8vmax "Roboto";
  border-bottom: 1px solid rgba(0, 0, 0, 0.267);
  padding: 1vmax;
  width: 100%;
  margin: auto;
  box-sizing: border-box;
}

.orderSummary > div > div {
  display: flex;
  font: 300 1vmax "Roboto";
  justify-content: space-between;
  margin: 2vmax 0;
}
.orderSummary > div > div > span {
  color: rgba(0, 0, 0, 0.692);
}

.orderSummaryTotal {
  display: flex;
  font: 300 1vmax "Roboto";
  justify-content: space-between;
  border-top: 1px solid rgba(0, 0, 0, 0.363);
  padding: 2vmax 0;
}

.payBtn {
  color: white;
  background: linear-gradient(
    90deg,
    rgb(4, 52, 101) 35%,
    rgb(10, 106, 174) 100%,
    rgba(15, 204, 141, 0.050945378151260545) 100%
  );
  width: 100%;
  padding: 1vmax;
  margin: auto;
  cursor: pointer;
  transition: 0.5s;
  font: 400 1.4vmax "Roboto";
  border: none;
  border-radius: 10px;
}

.payBtn:hover {
  color: white;
  background: linear-gradient(
    90deg,
    rgba(9, 82, 153, 1) 35%,
    rgba(47, 161, 245, 1) 100%,
    rgba(15, 204, 141, 0.050945378151260545) 100%
  );
}

@media screen and (max-width: 400px) {
  .confirmOrderPage {
    grid-template-columns: 1fr;
    height: unset;
    width: 100vw;
    overflow-x: hidden;
  }

  .confirmOrderPage > div:last-child {
    border-left: 0;
    /* width: 100%; */
  }

  .confirmshippingArea > p {
    font: 400 6vw "Roboto";
  }

  .confirmshippingAreaBox > div {
    display: flex;
    margin: 6vw 0;
  }

  .confirmshippingAreaBox > div > p {
    font: 400 4vw "Roboto";
  }
  .confirmshippingAreaBox > div > span {
    font: 100 4vw "Roboto";
  }

  .confirmCartItems > p {
    font: 400 6vw "Roboto";
  }

  .confirmCartItemsContainer {
    max-height: 50vw;
    width: 100vw;
    flex-wrap: wrap;
  }

  .confirmCartItemsContainer > div {
    font: 400 4vw "Roboto";
    margin: 4vw 0;
    width: 90%;
    flex-wrap: wrap;
  }

  .confirmCartItemsContainer > div > img {
    width: 20vw;
  }

  .confirmCartItemsContainer > div > a {
    margin: 0;
    width: 60%;
  }

  .confirmCartItemsContainer > div > span {
    font: 100 4vw "Roboto";
  }

  .orderSummary {
    padding: 2vw;
  }

  .orderSummary > p {
    font: 400 7vw "Roboto";
    /* padding: 4vw; */
    width: 100vw;
    border-bottom: none;
    text-align: center;
    padding-right: 70px;
  }

  .orderSummary > div > div {
    font: 300 4vw "Roboto";
  }

  .orderSummaryTotal {
    font: 300 4vw "Roboto";
    padding: 5vw 0;
    width: 90vw;
  }

  .orderSummary > button {
    padding: 4vw;
    margin: 4vw auto;
    font: 400 4vw "Roboto";
    width: 96vw;
  }
}
