.speedDial {
  position: fixed;
  right: 3vmax;
  top: 3vmax;
  z-index: 100;
}
.speedDialIcon {
  height: 56px;
  width: 56px;
  border-radius: 100%;
  z-index: 100;
}
.speedOptions {
  color: aliceblue !important;
  background: linear-gradient(
    90deg,
    rgb(4, 52, 101) 35%,
    rgb(10, 106, 174) 100%,
    rgba(15, 204, 141, 0.050945378151260545) 100%
  );
}
