.ProductDetails {
  display: flex;
  width: 100vw;
  max-width: 100%;
  padding: 6vmax;
  box-sizing: border-box;

  justify-content: space-evenly;
  align-items: center;
}
.carousel {
  overflow: visible !important;
  width: 100%;
  height: 400px !important;
}
.ProductDetails > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 2vmax;
  box-sizing: border-box;
}
.CarouselImage {
  width: 100% !important;
  height: 300px !important;
  object-fit: cover;
}
.ProductDetails > div:last-child {
  align-items: flex-start;
}

.CarouselImage {
  width: 20vmax;
}

.detailsBlock1 > h2 {
  color: rgb(54, 54, 54);
  font: 600 1.6vmax "Roboto";
}

.detailsBlock1 > p {
  margin-top: 10px;
  color: rgba(54, 54, 54, 0.582);
  font: 200 0.9vmax cursive;
}

.detailsBlock2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  width: 89%;
  padding: 1vmax 0;
  gap: 24px;
}

.detailsBlock2 h2 {
  font: 200 1vmax cursive;
  color: rgba(0, 0, 0, 0.699);
}

.detailsBlock3 {
  width: 70%;
}

.detailsBlock3 > h1 {
  color: rgba(17, 17, 17, 0.795);
  font: 400 1.8vmax "Franklin Gothic Medium";
  margin: 1vmax 0;
}
.detailsBlock3-1 {
  display: flex;
  align-items: center;
  /* flex-direction: column; */
  position: relative;
}
.detailsBlock3-1-1 {
  display: flex;
  justify-content: center;
  gap: 20px;
  align-items: center;
  margin-top: 20px;
}

.detailsBlock3-1-1 > button {
  border: none;
  background-color: rgba(0, 0, 0, 0.616);
  padding: 0.5vmax;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  height: 30px;
  display: flex;
  align-items: center;
  width: 26px;
  justify-content: center;
}
.detailsBlock3-1-1 > button:hover {
  background-color: rgba(0, 0, 0, 0.767);
}

.submitDialog {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detailsBlock3-1-1 > input {
  border: none;
  padding: 0.5vmax;
  width: 1vmax;
  text-align: center;
  outline: none;
  font: 400 0.8vmax "Roboto";
  color: rgba(0, 0, 0, 0.74);
}

.detailsBlock3-1 > button {
  border: none;
  cursor: pointer;
  color: white;
  transition: all 0.5s;
  background-color: rgb(7, 75, 123);
  font: 500 1vmax "Roboto";
  border-radius: 20px;
  padding: 0.5vmax 2vmax;
  margin: 1vmax;
  outline: none;
  cursor: pointer;
  margin-top: 30px;
}
.detailsBlock3-1 h4 {
  position: absolute;
  top: 0;
  left: 0;
}

.detailsBlock3-1 > button:hover {
  background-color: rgb(12, 104, 144);
}

.detailsBlock3 > p {
  border-top: 1px solid rgba(0, 0, 0, 0.205);
  border-bottom: 1px solid rgba(0, 0, 0, 0.205);
  padding: 1vmax 0;
  color: rgba(0, 0, 0, 0.651);
  font: 400 1vmax "Roboto";
  margin: 1vmax 0;
}

.detailsBlock4 {
  color: rgba(0, 0, 0, 0.897);
  font: 500 1.2vmax sans-serif;
}

.detailsBlock4 > p {
  color: rgba(0, 0, 0, 0.534);
  font: 300 0.8vmax sans-serif;
}

.submitReview {
  border: none;
  background-color: tomato;
  font: 500 0.7vmax "Roboto";
  border-radius: 20px;
  padding: 0.6vmax 2vmax;
  margin: 1vmax 0;
  color: white;
  cursor: pointer;
  transition: all 0.5s;
  outline: none;
}
.submitReview:hover {
  background-color: rgb(197, 68, 45);
  transform: scale(1.1);
}

.submitDialog {
  display: flex;
  flex-direction: column;
}
.submitDialogTextArea {
  border: 1px solid rgba(0, 0, 0, 0.082);
  margin: 1vmax 0;
  outline: none;
  padding: 1rem;
  font: 300 1rem "Roboto";
}

.reviewsHeading {
  color: #000000be;
  font: 500 1.4vmax "Roboto";
  text-align: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.226);
  padding: 1vmax;
  width: 20vmax;
  margin: auto;
  margin-bottom: 4vmax;
}
.reviews {
  display: flex;
  overflow: auto;
}

.reviewCard {
  flex: none;

  box-shadow: 0 0 5px rgba(0, 0, 0, 0.226);
  border: 1px solid rgba(56, 56, 56, 0.116);
  width: 30vmax;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1vmax;
  padding: 3vmax;
}
.reviewCard > img {
  width: 5vmax;
}
.reviewCard > p {
  color: rgba(0, 0, 0, 0.836);
  font: 600 0.9vmax "Roboto";
}
.reviewCard > span {
  color: rgba(0, 0, 0, 0.445);
  font: 300 0.8vmax cursive;
}

.noReviews {
  font: 400 1.3vmax "Gill Sans";
  text-align: center;
  color: rgba(0, 0, 0, 0.548);
}
.greenColor {
  color: green;
}
.redColor {
  color: red;
}
@media screen and (max-width: 400px) {
  .ProductDetails {
    flex-direction: column;
    height: unset;
    overflow-x: hidden;
  }

  .ProductDetails > div:last-child {
    align-items: center;
    padding-bottom: 90px;
  }

  .detailsBlock1 > h2 {
    font-size: 3.4vmax;
    text-align: center;
  }

  .detailsBlock1 > p {
    text-align: center;
    font-size: 1.7vmax;
  }

  .detailsBlock2 {
    justify-content: center;
    width: 90vw;
  }
  .detailsBlock2 > h2 {
    font-size: 1.6vmax;
  }

  .detailsBlock3 > h1 {
    font: 700 3vmax "Franklin Gothic Medium";
    text-align: center;
  }

  .detailsBlock3-1 {
    flex-direction: column;
  }
  .detailsBlock3-1 > h4 {
    width: 90vw;
  }

  .detailsBlock3-1-1 {
    padding: 2vmax 0;
    width: 90vw;
  }
  .detailsBlock3-1-1 > button {
    padding: 1.2vmax;
    width: 4vmax;
  }

  .detailsBlock3-1-1 > input {
    padding: 1.5vmax;
    width: 3vmax;
    font: 400 1.8vmax "Roboto";
  }

  .detailsBlock3-1 > button:last-child {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .detailsBlock3 > p {
    padding: 2.5vmax 0;
    text-align: center;
    font: 400 2vmax "Roboto";
  }

  .detailsBlock4 {
    font: 500 2.5vmax sans-serif;
  }

  .detailsBlock4 > p {
    font: 300 1.8vmax sans-serif;
  }

  .submitReview {
    font: 500 1.7vmax "Roboto";
    padding: 1.5vmax;
    width: 20vmax;
    margin: 3vmax 0;
  }

  .reviewCard > p {
    font: 600 3vw "Roboto";
  }
  .reviewCardComment {
    font: 300 5vw cursive;
  }
}
